import {
  SAVE_ADMIN_INFO,
  RECORD_ADDRESS,
  RESET_STATE,
  ADD_CART,
  REDUCE_CART,
  INIT_BUYCART,
  CLEAR_CART,
  RECORD_SHOPDETAIL,
  RECORD_USERINFO,
  GET_USERINFO,
  CONFIRM_REMARK,
  CONFIRM_INVOICE,
  CHOOSE_SEARCH_ADDRESS,
  SAVE_GEOHASH,
  CONFIRM_ADDRESS,
  CHOOSE_ADDRESS,
  NEED_VALIDATION,
  SAVE_CART_ID_SIG,
  SAVE_ORDER_PARAM,
  CHANGE_ORDER_PARAM,
  ORDER_SUCCESS,
  SAVE_SHOPID,
  SAVE_ORDER,
  OUT_LOGIN,
  RETSET_NAME,
  SAVE_AVANDER,
  SAVE_ADDRESS,
  SAVE_ADDDETAIL,
  SAVE_QUESTION,
  ADD_ADDRESS,
  BUY_CART
} from './mutation-types.js'

import { setStore, getStore } from '../config/mUtils'

export default {
  [RESET_STATE](state) {
    Object.assign(state, {
      adminInfo: {
        avatar: 'default.jpg',
        status: 0 //0 1 2
      },
      latitude: '', // 
      longitude: '', // 
      cartList: [], // 
      restaurantInfo: null, // 
      userInfo: null, // 
      shopid: null, // id
      remarkText: null, // 
      inputText: '', // 
      invoice: false, // 
      newAddress: [], // 
      searchAddress: null, // 
      geohash: '31.22299,121.36025', // geohash
      choosedAddress: null, // 
      addressIndex: null, // 
      needValidation: null, // 
      cartId: null, // id
      sig: null, // sig
      orderParam: null, // 
      orderMessage: null, // 
      orderDetail: null, // 
      login: true, // 
      imgPath: null, // 
      removeAddress: [], // 
      addAddress: '', // 
      question: null, // 
      cartPrice: null // 
    })
  },
  [SAVE_ADMIN_INFO](state, userInfo) {
    state.userInfo = userInfo
  },
  // 
  [RECORD_ADDRESS](state, { latitude, longitude }) {
    state.latitude = latitude
    state.longitude = longitude
  },

  [RECORD_SHOPDETAIL](state, detail) {
    state.restaurantInfo = detail
  },
  // 
  [ADD_CART](
    state,
    { shopid, food_id, name, price, specs, nameWithSpecs, extra, food = '' }
  ) {
    let isHave = false
    let index = null
    let paramsExtraString = extra.map(item => item.name).toString()
    // 

    for (let i = 0; i < state.cartList.length; i++) {
      let originalSpecs = state.cartList[i].specs
      let originalExtra = state.cartList[i].extra
        .map(item => item.name)
        .toString()
      if (
        state.cartList[i].id === food_id &&
        originalSpecs === specs &&
        originalExtra === paramsExtraString
      ) {
        isHave = true
        index = i
      }
    }

    if (isHave) {
      // 
      if (food) {
        food.num--
      }
      let arr = state.cartList.filter(o => state.cartList.indexOf(o) === index)
      arr[0].num++
    } else {
      if (food) {
        if (food.num === 1) {
          food.extra = extra
        } else {
          let newFoodObject = {
            shopId: shopid,
            num: 1,
            name,
            id: food_id,
            price,
            specs,
            nameWithSpecs,
            extra
          }
          food.num--
          state.cartList.push(newFoodObject)
        }
      } else {
        // 
        let newFoodObject = {
          shopId: shopid,
          num: 1,
          name,
          id: food_id,
          price,
          specs,
          nameWithSpecs,
          extra
        }

        state.cartList.push(newFoodObject)
      }
    }
    state.cartList = state.cartList.filter(food => food.num !== 0)
    // localStorage
    setStore('buyCart', state.cartList)
  },
  // 
  [REDUCE_CART](state, { index }) {
    // 
    for (let i = 0; i < state.cartList.length; i++) {
      if (i === index) {
        if (state.cartList[i].num > 1) {
          state.cartList[i].num--
        } else {
          state.cartList = state.cartList.filter(
            o => state.cartList.indexOf(o) !== index
          )
        }
      }
    }
  },
  // 
  [REDUCE_CART](state, { index }) {
    // 
    for (let i = 0; i < state.cartList.length; i++) {
      if (i === index) {
        if (state.cartList[i].num > 1) {
          state.cartList[i].num--
        } else {
          state.cartList = state.cartList.filter(
            o => state.cartList.indexOf(o) !== index
          )
        }
      }
    }
  },
  // 
  [INIT_BUYCART](state) {
    let initCart = getStore('buyCart')
    if (initCart) {
      state.cartList = JSON.parse(initCart)
    }
  },
  // 
  [CLEAR_CART](state) {
    state.cartList = []
    setStore('buyCart', state.cartList)
  },
  // 
  [RECORD_USERINFO](state, info) {
    state.userInfo = info
    state.login = true
    setStore('user_id', info.user_id)
  },
  // vuex
  [GET_USERINFO](state, info) {
    if (state.userInfo && state.userInfo.username !== info.username) {
      return
    }
    if (!state.login) {
      return
    }
    if (!info.message) {
      state.userInfo = { ...info }
    } else {
      state.userInfo = null
    }
  },
  // 
  [RETSET_NAME](state, username) {
    state.userInfo = Object.assign({}, state.userInfo, { username })
  },
  // id
  [SAVE_SHOPID](state, shopid) {
    state.shopid = shopid
  },
  // , 
  [CONFIRM_REMARK](state, { remarkText, inputText }) {
    state.remarkText = remarkText
    state.inputText = inputText
  },
  // 
  [CONFIRM_INVOICE](state, invoice) {
    state.invoice = invoice
  },
  // 
  [CHOOSE_SEARCH_ADDRESS](state, place) {
    state.searchAddress = place
  },
  // geohash
  [SAVE_GEOHASH](state, geohash) {
    state.geohash = geohash
  },
  // 
  [CONFIRM_ADDRESS](state, newAddress) {
    state.newAddress.push(newAddress)
  },
  // 
  [CHOOSE_ADDRESS](state, { address, index }) {
    state.choosedAddress = address
    state.addressIndex = index
  },
  // 
  [NEED_VALIDATION](state, needValidation) {
    state.needValidation = needValidation
  },
  // id  sig
  [SAVE_CART_ID_SIG](state, { cart_id, sig }) {
    state.cart_id = cart_id
    state.sig = sig
  },
  // 
  [SAVE_ORDER_PARAM](state, orderParam) {
    state.orderParam = orderParam
  },
  // 
  [CHANGE_ORDER_PARAM](state, newParam) {
    state.orderParam = Object.assign({}, state.orderParam, newParam)
  },
  // 
  [ORDER_SUCCESS](state, order) {
    state.cartPrice = null
    state.orderMessage = order
  },
  // 
  [SAVE_ORDER](state, orderDetail) {
    state.orderDetail = orderDetail
  },
  // 
  [OUT_LOGIN](state) {
    state.userInfo = {}
    state.login = false
  },
  // 
  [SAVE_AVANDER](state, imgPath) {
    state.imgPath = imgPath
  },
  // 
  [SAVE_ADDRESS](state, newAdress) {
    state.removeAddress = newAdress
  },
  // name
  [SAVE_ADDDETAIL](state, addAddress) {
    state.addAddress = addAddress
  },
  // 
  [SAVE_QUESTION](state, question) {
    state.question = { ...question }
  },
  // 
  [ADD_ADDRESS](state, obj) {
    state.removeAddress = [obj, ...state.removeAddress]
  },
  // 
  [BUY_CART](state, price) {
    state.cartPrice = price
  }
}
