import axios from 'axios'
import { Message } from 'element-ui'
// import NProgress from 'nprogress'

// 'http://119.23.201.183:4000'
const instance = axios.create({
  baseURL:
    process.env.NODE_ENV === 'development'
      ? 'http://127.0.0.1:4000/api'
      : 'http://pos.migaox.com/api', // apibase_url
  timeout: 20000 // 
})

let timer

//
instance.interceptors.request.use(
  config => {
    // config.url = '/api' + config.url
    const token = localStorage.getItem('token')
    const Captcha = localStorage.getItem('captcha')
    if (token) {
      config.headers.common['Authorization'] = 'Bearer ' + token
    }
    if (Captcha) {
      config.headers.common['Captcha'] = 'Bearer ' + Captcha
    }

    return config
  },
  error => {
    Message.error('bed request')
    Promise.reject(error)
  }
)
//
instance.interceptors.response.use(
  response => {
    if (response.data.status === 0) {
      response.data.message && Message.error(response.data.message)
      return Promise.reject(response.data)
    }
    return response.data
  },
  err => {
    clearTimeout(timer)
    timer = setTimeout(() => {
      if (err && err.response) {
        switch (err.response.status) {
          case 400:
            Message.error('')
            break
          case 401:
            localStorage.clear()
            Message.error('')
            break
          case 403:
            Message.error('')
            break
          case 404:
            Message.error(',')
            break
          case 500:
            Message.err('')
            break
          default:
            Message.err(` ${err.response.status}`)
            break
        }
      } else {
        Message.error('')
      }
    }, 200) // 200 

    return Promise.reject(err)
  }
)

export default instance
